.App {
  text-align: left;  
}

.App__container {
  max-width: 750px;
  margin: 0 auto;
  padding: 5px 30px;
}

.App__body {
  display: flex;
  justify-content: space-between;
}

.App__total {
  text-align: center;
}

@media (max-width: 800px) {
  .App__container {
    max-width: 450px;
    padding: 5px 10px;
  }

  .App__body {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
}