.TextInputExp {
  max-width: 330px;
  background-color: rgba(233, 240, 240, 0.945);  
}

.TextInputExp__body {
  padding: 5px 10px;
  margin: 10px 0;
}

.TextInputExp__item {
    font-family: 'Arial', serif;
    font-size: 18px;
    width: 300px;
  }

  @media (max-width: 400px) {
    .TextInputExp {
        max-width: 280px;
    }
    .TextInputExp__item {
      width: 250px;
    }
}