.TextInput {
  max-width: 330px;
  background-color: rgba(240, 247, 247, 0.945);  
}

.TextInput__body {
  padding: 5px 10px;
  margin: 10px 0;
}

.TextInput__item {
    font-family: 'Arial', serif;
    font-size: 18px;
    width: 300px;
  }

  @media (max-width: 400px) {
    .TextInput {
      max-width: 280px;
    }
    .TextInput__item  {
      width: 250px;
    }
}