.message {
    display: flex;
    align-items: center;
    max-width: 320px;
    padding: 5px;
    background-color: rgba(248, 242, 242, 0.836);
}

.message:hover {
    background-color: rgba(223, 219, 219, 0.836);
    cursor: pointer;
}

.message__item {
    display: block;
    width: 250px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    padding: 5px 10px;
    color: rgb(6, 155, 13);    
}

.message__btnRemove {
    background-color: red;
    color: white;
    cursor: pointer;
    border: 1px solid rgb(109, 5, 1);
    border-radius: 50%;
}

.message__btnRemove:hover  {
    background-color: rgb(207, 4, 4);
}

@media (max-width: 400px) {
    .message {
        max-width: 280px;
    }
}